import { Action } from '../Types'

export default (state = {planData: {}, isLoaded: false}, action) => {
  switch (action.type) {
    case Action.Create.PlanData:
      return {...state, [action.payload.area]: action.payload.data, isLoaded: true}
    case Action.Fetch.PlanData:
      return {...state, isLoaded: false}
    default:
      return state
  }
}