import { connect } from 'react-redux'

import {
  updateBirthdateMonth,
  updateBirthdateDate,
  updateBirthdateYear,
  deleteBirthdate,
  decrementEmployeeCount,
  createBirthdate,
  showBirthdates
} from '../actions'

import Birthdates from '../components/Birthdates'

const mapStateToProps = (state) => {
 return{
  EmployeeCount: state.EmployeeCount,
  Birthdates: state.Birthdate,
  showingBirthdates: state.UI.showingBirthdates
}}

const mapDispatchToProps = (dispatch) => ({
  showBirthdates: () => dispatch(showBirthdates()),
  ChangeHandlers: {
    Mounted: () => dispatch(createBirthdate()),
    Month:   (index, event) => dispatch(updateBirthdateMonth(index, event.target.value || 0)),
    Date:    (index, event) => dispatch(updateBirthdateDate( index, event.target.value || 0)),
    Year:    (index, event) => dispatch(updateBirthdateYear( index, event.target.value || 0)),
    Delete:  (index) => {
     dispatch(deleteBirthdate( index ))
     dispatch(decrementEmployeeCount())
    }
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Birthdates)
