import React from 'react'

export default ({ Index, ChangeHandler, Value, inputRef, ...props }) =>
  <input
    id={ `date-${Index}-value` }
    className="form-control date"
    type="number"
    value={Value || ""}
    min="1"
    max="31"
    placeholder="00"
    onChange={ ChangeHandler }
    ref={inputRef}
    {...props} />
