import React from 'react'

export default ({ Index, ChangeHandler, Value, inputRef, ...props }) =>
  <input
    id={ `month-${Index}-value` }
    className="form-control month"
    type="number"
    value={Value || ""}
    min="1"
    max="12"
    placeholder="00"
    onChange={ ChangeHandler }
    ref={inputRef}
    {...props} />
