import { Action } from '../Types'

export const updateZip = zip => ({
  type: Action.Update.Zip,
  zip: parseInt(zip, 10)
})

export const updateEffectiveMonth = month => ({
  type:                  Action.Update.EffectiveMonth,
  updatedEffectiveMonth: parseInt(month, 10)
})

export const updateEmployeeCount = employeeCount => ({
  type: Action.Update.EmployeeCount,
  employeeCount: employeeCount
})

export const decrementEmployeeCount = () => ({
 type: Action.Decrement.EmployeeCount
})

export const showBirthdates = () => ({
  type: Action.Show.Birthdates
})

export const createBirthdate = () => ({
  type: Action.Create.Birthdate,
  month: "",
  date: "",
  year: ""
})

export const updateBirthdateMonth = (index, month) => ({
  type: Action.Update.Birthdate.Month,
  index: parseInt(index, 10),
  month: parseInt(month, 10)
})

export const updateBirthdateDate = (index, date) => ({
  type: Action.Update.Birthdate.Date,
  index: parseInt(index, 10),
  date: parseInt(date, 10)
})

export const updateBirthdateYear = (index, year) => ({
  type: Action.Update.Birthdate.Year,
  index: parseInt(index, 10),
  year: parseInt(year, 10)
})

export const deleteBirthdate = (index) => ({
  type: Action.Delete.Birthdate,
  index: parseInt(index, 10)
})

export const fetchPlanData = (area, quarter) => async dispatch => {
 const path = `/plan-data-area-${area}-Q${quarter}.json`
 try {

  dispatch(setPlanDataAsFetching())

  const result = await fetch(path)
  if(result) {
   const json = await result.json()
   dispatch(savePlanData(json, area))
  }
 } catch (error) {
  console.error('Error fetching plan data. Attempted path:', path, error)
 }
}

export const savePlanData = (data, area) => {
  return {
   type: Action.Create.PlanData,
   payload: {data, area}
  }
}

export const setPlanDataAsFetching = () => {
 return {
  type: Action.Fetch.PlanData
 }
}
