import { connect } from 'react-redux'
import { updateEffectiveMonth } from '../actions'
import EffectiveMonth from '../components/EffectiveMonth'
import * as $ from 'jquery'

const mapStateToProps = (state) => ({
  EffectiveMonth: state.EffectiveMonth
})

const mapDispatchToProps = (dispatch) => ({
  ChangeHandler: (event) => {
    const value = event.target.value || 0

    if (value !== -1) {
      $('.filter-option-inner-inner').addClass('not-placeholder')
    }

    dispatch(updateEffectiveMonth(value))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(EffectiveMonth)
