import { connect } from 'react-redux'
import 'babel-polyfill'

import * as $ from 'jquery'

import { updateZip } from '../actions'

import Zip from '../components/Zip'

import { AVAILABLE_ZIP_CODES } from '../Constants'

const mapStateToProps = (state) => ({
  Zip: state.Zip
})

const mapDispatchToProps = (dispatch) => ({
  ChangeHandler: (event) => {
    const val    = parseInt(event.target.value, 10)
    const digits = event.target.value.length

    if (digits < 5) {
      $('#zip-error').tooltip('disable')
      $('#zip-error').tooltip('hide')
      $('#zip-error').removeClass('has-error')
    } else if (digits === 5) {
      const zipIsValid = AVAILABLE_ZIP_CODES.includes(val) === true

      $('#zip-error').tooltip(zipIsValid ? 'disable' : 'enable')
      $('#zip-error').tooltip(zipIsValid ? 'hide' : 'show')

      if (zipIsValid) {
        $('#zip-error').removeClass('has-error')
      } else {
        $('#zip-error').addClass('has-error')
      }
    } else if (digits > 5) {
      $('#zip-error').tooltip('enable')
      $('#zip-error').tooltip('show')
      $('#zip-error').addClass('has-error')
    }

    dispatch(updateZip(val))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Zip)
