import React from 'react'
import { EMPLOYEE_YEAR_MAX, EMPLOYEE_YEAR_MIN } from '../../Constants';

export default ({ Index, ChangeHandler, Value, inputRef, ...props }) => {
  return <input
    id={ `year-${Index}-value` }
    className="form-control year"
    type="number"
    value={Value || ""}
    min={EMPLOYEE_YEAR_MIN}
    max={EMPLOYEE_YEAR_MAX}
    placeholder="0000"
    onChange={ ChangeHandler }
    ref={inputRef}
    {...props}
    />
}
