import React from 'react'
import MediaQuery from 'react-responsive'

import '@fortawesome/fontawesome-free/css/all.css'

import './style.css'

const Mobile = props => <MediaQuery {...props} maxWidth={767} />

export default ({ ChangeHandler }) =>
  <div id="zip" className="row form-group">
    <label htmlFor="zip-value" className="col-md-8 col-12">
      To help us get a more accurate quote, please provide your zip code:
    </label>
    <div className="col-md-4 col-12">
      <Mobile>
        {(isMobile) => (
          <span
            id="zip-error"
            className="d-inline-block"
            data-toggle="tooltip"
            data-placement={isMobile ? "bottom" : "right"}
            data-html="true"
            title="<h4><i class='fas fa-exclamation-circle'></i> Error</h4><p>Sorry! There are no Simplete plans currently available in your area. Please check back next year as we hope to expand.</p>">
            <input
              id="zip-value"
              className="col form-control"
              type="number"
              placeholder="Zipcode"
              onChange={ ChangeHandler } />
          </span>
        )}
      </Mobile>
    </div>
  </div>
