import { Action } from '../Types'

export default (state = { Value: "" }, action) => {
  switch (action.type) {
    case Action.Update.EmployeeCount:
      return { Value: action.employeeCount > 500 ? 500 : action.employeeCount }
    case Action.Decrement.EmployeeCount:
      return { Value: state.Value > 0 ? state.Value - 1 : "" }
    default:
      return state
  }
}
