import { connect } from 'react-redux'
import { updateEmployeeCount } from '../actions'
import EmployeeCount from '../components/EmployeeCount'
import * as $ from 'jquery'

const mapStateToProps = (state) => ({
  EmployeeCount: state.EmployeeCount
})

const mapDispatchToProps = (dispatch) => ({
  ChangeHandler: (event) => {
    const value = event.target.value || ""

    if (value === "" || value === 0) {
      $('#employee-count-value').addClass('placeholder')
    } else {
      $('#employee-count-value').removeClass('placeholder')
    }

    dispatch(updateEmployeeCount(value))
  }
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(EmployeeCount)
