import { Action } from '../Types'
import { EMPLOYEE_YEAR_MAX, EMPLOYEE_YEAR_MIN } from '../Constants';

export default (state = [ ], action) => {
  const index = action.index

  switch (action.type) {
    case Action.Create.Birthdate:
      return [...state, { month: action.month, date: action.date, year: action.year }]
    case Action.Update.Birthdate.Month:
      const month = action.month > 12 ? 12 : action.month < 0 ? 0 : action.month
      return state.map((item, i) => i === index ? {...item, month} : item )

    case Action.Update.Birthdate.Date:
      const date = action.date > 31 ? 31 : action.date < 0 ? 0 : action.date
      return state.map((item, i) => i === index ? {...item, date} : item )

    case Action.Update.Birthdate.Year:
      const year = action.year > EMPLOYEE_YEAR_MAX ? EMPLOYEE_YEAR_MAX : action.year > 999 && action.year < EMPLOYEE_YEAR_MIN ? EMPLOYEE_YEAR_MIN : action.year
      return state.map((item, i) => i === index ? {...item, year } : item )

    case Action.Delete.Birthdate:
      return state.filter((item, i) => i !== index)

    default:
      return state
  }
}