import React from 'react'

import Birthdate from '../../components/Birthdate'

import './style.css'

export default ({ EmployeeCount, ChangeHandlers, Birthdates, showingBirthdates, showBirthdates }) => {
  if (!showingBirthdates) {
    return (
      <div className="row show-birthdates">
      <button
        type="button"
        className='btn btn-primary btn-lg'
        onClick={showBirthdates}
        disabled={EmployeeCount.Value === "" || EmployeeCount.Value === '0'}>
        Next
      </button>
      </div>
    )
  }

  let birthdates = [ ]
  let limit

  if (EmployeeCount && EmployeeCount.Value)
    limit = EmployeeCount.Value
  else
    limit = 0

  for (let i = 0; i < limit; i++)
    birthdates.push(
      <Birthdate
        key={ `birthdate-${i}`}
        Index={ i }
        ChangeHandlers={ ChangeHandlers }
        Birthdates={ Birthdates }
         />)

  return <div id="employee-birthdates">
    <div className="row">
      <h3 className="col text-center d-none d-md-block">Enter the birthdates of all employees and dependents you plan to cover below</h3>
    </div>
    {limit > 0 && <div className="row">
      <div className="col-md-6 col-12">
        <div className="spacer" />
        <h4 className="col-3">Month</h4>
        <h4 className="col-3">Day</h4>
        <h4 className="col-5">Year</h4>
      </div>
    </div>}
    <div className="row">
      { birthdates }
    </div>
  </div>
}
