import moment from 'moment';
import _      from 'lodash';

import {
  EMPLOYEE_YEAR_MIN,
  AVAILABLE_ZIP_CODES
} from '../Constants';

export const isValidBirthdate = (birthdate) =>
  _.isNumber(birthdate.month) && birthdate.month >  0 &&
  _.isNumber(birthdate.date)  && birthdate.date  >  0 &&
  _.isNumber(birthdate.year)  && birthdate.year  >= EMPLOYEE_YEAR_MIN;

export const userQuoteFormDataIsInvalid = ({ EmployeeCount, Zip, Birthdate }) =>
  (EmployeeCount.Value === "" || EmployeeCount.Value === '0') ||
  Birthdate.length    === 0                        ||
 !Birthdate.every(isValidBirthdate)                ||
(!Zip.Value || !Zip.Value.toString().length === 5) ||
 !AVAILABLE_ZIP_CODES.includes(Zip.Value);

export const generateTotals = ({ Birthdate, QuoteData, EffectiveMonth }) => {
  if (Birthdate === undefined || QuoteData === undefined || EffectiveMonth === undefined)
    return

  const validBirthdates = Birthdate.filter(isValidBirthdate);

  if(_.isEmpty(Birthdate) || _.isEmpty(QuoteData) || (!validBirthdates || validBirthdates.length === 0))
    return [ ];

  const currentYear   = moment().format('YYYY');
  const effectiveDate = moment(`${EffectiveMonth.Value + 1}-01-${currentYear}`, 'MM-DD-YYYY');

  const ages = validBirthdates.map(item =>
    effectiveDate.diff(
        moment(`${item.month}-${item.date}-${item.year}`, 'MM-DD-YYYY'),
        'years'
      )
  );

  const plans = QuoteData["10"];
  const reduced = _.reduce(plans, (acc, plan, key) => {
    const val = ages.reduce((_acc, age) => _acc + (age < 64 ? plan[age] : plan['64+']), 0)
    return { ...acc, [key] : val }
  }, {});

  return reduced;
}
