import React from 'react'

import { Provider } from 'react-redux'
import { createStore, applyMiddleware } from 'redux'
import rootReducer from '../../reducers'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faTrashAlt, faCoffee, faTrash } from '@fortawesome/free-solid-svg-icons'
import thunk from 'redux-thunk'
import { composeWithDevTools } from 'redux-devtools-extension'

import Zip from '../../containers/Zip'
import EffectiveMonth from '../../containers/EffectiveMonth'
import EmployeeCount from '../../containers/EmployeeCount'
import Birthdates from '../../containers/Birthdates'
import Quote from '../../containers/Quote'
import ColorBar from '../../components/ColorBar'
import Title from '../Title'

import './style.css'

library.add(faTrashAlt, faCoffee, faTrash)

const middlewareEnhancer = applyMiddleware(thunk)
const composedEnhancers = composeWithDevTools(middlewareEnhancer)
const store = createStore(rootReducer, composedEnhancers)

export default () =>
  <Provider store={store}>
    <div id="quote-calculator" className="container">
      <Title Hero="Get an Instant Quote" />
      <div id="calculator-wrapper">
        <ColorBar />
        <form>
          <Zip />
          <EffectiveMonth />
          <EmployeeCount />
          <Birthdates />
          <Quote />
        </form>
      </div>
    </div>
  </Provider>
