import React, {Component} from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import Month from './Month'
import Date  from './Date'
import Year  from './Year'

export default class BirthdayFields extends Component {
  dateInput = React.createRef()
  yearInput = React.createRef()

  componentDidMount() {
    const { ChangeHandlers } = this.props
    ChangeHandlers.Mounted()
  }

  onKeyUp = (maxLength, ref) => event => {
    if (event.target.value.length >= maxLength) {
      ref.current.focus()
    }
  }

  render() {
    const { Index, ChangeHandlers, Birthdates } = this.props
    return <div className="col-md-6 col-12 form-group employee-row">
        <label htmlFor={ `month-${Index}-value` } className="text-center row-number">{ Index + 1 }.</label>
        <Month Index={ Index } ChangeHandler={ event => ChangeHandlers.Month(Index, event) } Value={Birthdates[Index] && Birthdates[Index].month}
               onKeyUp={this.onKeyUp(2, this.dateInput)}/>
        <Date  Index={ Index } ChangeHandler={ event => ChangeHandlers.Date(Index, event)  } Value={Birthdates[Index] && Birthdates[Index].date}
               onKeyUp={this.onKeyUp(2, this.yearInput)} inputRef={this.dateInput}/>
        <Year  Index={ Index } ChangeHandler={ event => ChangeHandlers.Year(Index, event)  } Value={Birthdates[Index] && Birthdates[Index].year}
               inputRef={this.yearInput}/>
        <button type="button" className="btn btn-link" onClick={() => ChangeHandlers.Delete(Index)}>
          <FontAwesomeIcon className="trash" icon="trash-alt" size="lg" />
        </button>
      </div>
  }
}
