import React, {Component} from 'react'
import classNames from 'classnames'
import './style.css'
import { generateTotals, userQuoteFormDataIsInvalid } from '../../utils/quote-util'
import ColorBar from '../../components/ColorBar'
import numeral from 'numeral'
import moment from 'moment'

const hydrateTemples = (array) => {
  let out = [ ]

  for (let i in array)
    out.push(
      <div className="details" key={ array[i].name }>
        <div>
          <p className="lead">{ array[i].name }</p>
        </div>
        <div>
        <p className="lead text-right">{ numeral(array[i].premium).format('$0,0[.]00') }</p>
        </div>
        <div className="divider"></div>
      </div>
    )

  return out
}

export default class Quote extends Component {

  componentDidUpdate(prevProps) {
    const { EffectiveMonth } = this.props
    if(EffectiveMonth.Value !== prevProps.EffectiveMonth.Value && EffectiveMonth.Value > -1) {
      this.props.fetchPlanData(10, moment({month: EffectiveMonth.Value}).quarter())
    }
  }

  render() {
    const { showingBirthdates, QuoteData } = this.props
    const isDisabled = !QuoteData.isLoaded || userQuoteFormDataIsInvalid(this.props)
    const totals    = generateTotals(this.props)

    let hmoPlans = [ ]
    let posPlans = [ ]

    for (let plan in totals) {
      if (plan.indexOf('HMO') === 0)
        hmoPlans.push({ name: plan, premium: totals[plan] })
      else if (plan.indexOf('POS') === 0)
        posPlans.push({ name: plan, premium: totals[plan] })
    }

    const sortDescending = (a, b) => b.premium - a.premium

    hmoPlans.sort(sortDescending)
    posPlans.sort(sortDescending)

    return <div id="quote" className="w-50 p-3 mx-auto row d-flex justify-content-center">
    {showingBirthdates && <button
      type="button"
      className={classNames('btn btn-primary btn-lg btn-get-quote', {'btn-dimmed': isDisabled})}
      data-toggle="modal"
      data-target="#quote-modal">
      Get Your Quote
    </button>}

    {!isDisabled && <div
      className="modal fade"
      id="quote-modal"
      tabIndex="-1"
      role="dialog"
      aria-labelledby="quote-model-label"
      aria-hidden="true">
      <div className="modal-dialog modal-dialog-centered premiums" role="document">
        <div className="modal-content">
          <ColorBar />
          <div className="modal-body">
            <h1 className="modal-title text-center congratulations">Congratulations!</h1>
            <p className="lead text-center">Here Are Your Simplete Quotes</p>
            <p className="text-center disclaimer">
              This is not a final quote. The rates displayed are valid for
              30 days and subject to change on a calendar quarter basis.
            </p>
            <div className="container-fluid">
              <div className="row plans">
                <div className="col-md-6">
                  <div className="hmo">
                    <h2>HMO Plans</h2>
                    { hydrateTemples(hmoPlans) }
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="pos">
                    <h2>POS Plans</h2>
                    { hydrateTemples(posPlans) }
                  </div>
                </div>
              </div>
              <div className="row call-to-action">
                <p className="text-center">
                  If you are interested in more information or would like a final quote,
                  click below to answer a few quick questions and someone will be in touch soon.
                </p>
                <a className="btn btn-lg btn-primary" href="/get-in-touch" target="_blank" rel='noopener noreferrer'>Get In Touch</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>}
  </div>
  }
}
